<template>
    <div class="search-result-box">
        <van-nav-bar  left-text="返回" left-arrow @click-left="back">
            <template #right>
                <van-search v-model="searchname" placeholder="请输入搜索关键词" shape="round" @search="getProductData" />
            </template>
        </van-nav-bar>
        <Bg>
            <div class="pro-items">
                <div class="pro-item" v-for="(item,index) in productData" :key="index">
                    <div class="pro-img">
                        <img :src="item.smallImg" class="auto-img" @click="enterDetail(item.pid)">
                    </div>
                    <div class="pro-info">
                        <div class="ch-name">{{item.name}}</div>
                        <div class="en-name">{{item.enname}}</div>
                        <div class="pro-price">&yen;{{item.price}}</div>
                    </div>
                </div>
            </div>

        </Bg>
    </div>
</template>

<script>
    import '../assets/less/searchresult.less'
    import Bg from '../components/Bg'
    export default {
        
        name:"SearchResult",
        data(){
            return {
                productData:[],
                goodsname:'',
                searchname:''
            }
        },
        methods:{
            back(){
                this.$router.go(-1);
            },
            //
            getProductData(){
                //不为空
                
                if(this.searchname){
                    if(this.searchname == this.goodsname){
                        return ;
                    }
                    this.goodsname = this.searchname;
                    //加载提示
                    this.$toast.loading({
                        message: '加载中...',
                        forbidClick: true,
                        duration:0
                    });
                    this.axios({
                        method:'GET',
                        url:this.baseUrl + '/search',
                        params:{
                            appkey:this.appkey,
                            name:this.searchname
                        }
                    }).then(res => {
                        this.$toast.clear();
                        if(res.data.code=='Q001'){
                            this.productData = res.data.result;
                        }
                    })
                }
            },
            enterDetail(pid){
                this.$router.push({name:'Detail',params:{pid}})
            }
        },
        components:{
            Bg
        },
        created(){
            this.searchname = this.$route.params.goodsname;
            this.getProductData();
        }
    }
</script>

